.nav {
  background-color: rgba(11, 13, 17, 0.479); }
  .nav .navbar-form {
    margin-right: 0px; }

.navbar-overlay {
  background-image: url("../imgs/bg-well.jpg"); }

#dashboard.container-fluid {
  background-color: rgba(98, 128, 189, 0.479); }

#dashboard input.search {
  border: 0 !important;
  border-bottom: 3px solid #10b834 !important;
  border-radius: 0px !important;
  box-shadow: 0px !important; }

#dashboard ul > li {
  margin-top: 3px; }
  #dashboard ul > li > a:hover {
    background-color: inherit;
    border-radius: 0px;
    height: 60px;
    border-bottom: 7px solid white; }

#dashboard span.fa,
#dashboard strong {
  color: white; }

#dashboard .fa-search:hover {
  font-weight: bolder; }

#dashboard i.fa-plus-circle {
  float: right;
  background-color: white;
  border-radius: 50%; }

#dashboard .col-md-5 > #post {
  background-color: white; }

@media (min-width: 992px) {
  #dashboard .nav-tabs .search {
    min-width: 300px; }
  #dashboard .nav-tabs li {
    margin: 0em 1.5em; } }

span > .badge {
  margin-bottom: 20px;
  margin-left: -12px;
  background-color: #a11f1f; }

.fa-ellipsis-v,
.fa-ellipsis-h {
  font-weight: bold;
  color: #161064;
  float: right; }

.row.posts {
  background-color: rgba(82, 80, 80, 0.3);
  margin: 2px; }
  .row.posts #post {
    background-color: white; }
    .row.posts #post hr {
      margin: -30px 0 0 0;
      border-bottom: 1px solid #3a3939; }
  .row.posts .media {
    padding: 7px 12px; }
  .row.posts #reply {
    padding: 0 !important; }
    .row.posts #reply h5 {
      font-weight: bold; }
  .row.posts #photo img {
    max-height: 300px;
    display: block;
    margin-left: auto;
    margin-right: auto; }

#description {
  width: 100%;
  overflow-wrap: break-word;
  margin-top: 5px;
  padding: 0px 12px 7px 12px;
  text-align: left;
  font-family: 'Trebuchet MS', 'Lucida Sans Unicode', 'Lucida Grande', 'Lucida Sans', Arial, sans-serif; }

#comments {
  width: 100%;
  overflow-wrap: break-word;
  text-align: left;
  margin-left: 45px; }

#reply-summary {
  padding-left: 15px;
  padding-top: 7px;
  font-style: italic; }

#response {
  margin-left: 45px;
  text-align: left;
  margin-bottom: 5px; }
  #response a:not(:nth-child(4)) {
    border-right: 2px solid grey;
    padding-right: 4px; }

#post-reply {
  margin-left: 45px; }

#more-reviews {
  padding: 15px; }
  #more-reviews:hover {
    cursor: pointer; }

.media-body {
  overflow-x: hidden; }
  .media-body h4 {
    margin: 0 !important; }
  .media-body p {
    margin: 0 !important; }
  .media-body .comment input.btn {
    margin: 5px 0;
    font-weight: bold; }

#post-options {
  position: relative;
  top: -50px;
  color: white;
  background-color: rgba(7, 7, 7, 0.589);
  padding: 10px;
  text-align: center;
  height: 50px; }
  #post-options .fa {
    font-size: 23px;
    margin: 0 5px 0 10px; }
